*{font-family:"Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif}body{overflow:hidden;background:none !important}#root{display:none;height:100vh}#characters{-webkit-user-select:none;-ms-user-select:none;user-select:none;height:100%;align-items:center;flex-direction:column}#characters .characters-stack{margin-top:64px;color:#fff;text-align:center}#characters .characters-stack .name{font-size:4rem;font-weight:600}#characters .characters-stack .onboarding{margin-top:-16px;opacity:0.5;font-size:2rem;font-weight:300}#characters .characters-grid{margin-top:auto;margin-bottom:64px;display:flex;justify-content:space-between;width:100%;max-width:1500px;padding:0 24px}#characters .characters-grid .character{display:flex;width:324px;height:86px;padding:8px 10px 8px 16px;background-color:#4c649e}#characters .characters-grid .character.empty{color:#fff;font-size:1.25rem;font-weight:600;align-items:center;justify-content:center;transition:opacity 0.2s}#characters .characters-grid .character.empty:hover{opacity:0.8}#characters .characters-grid .character .info{color:#fff;margin:auto 0}#characters .characters-grid .character .info .name{max-width:180px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;font-size:1.15rem;font-weight:00}#characters .characters-grid .character .info .bank{display:flex;font-size:0.85rem}#characters .characters-grid .character .info .bank .detail{margin-left:4px;font-weight:200;opacity:0.8}#characters .characters-grid .character .actions{margin-left:auto;display:flex;flex-direction:column;justify-content:space-between}#characters .characters-grid .character .actions>button{color:#d7d8cd}#characters .characters-grid .character .actions>button:first-of-type{background-color:#497c00}#characters .characters-grid .character .actions>button:nth-of-type(2){background-color:#af3600}

